/* âœ…Reset */
body,
ul,
p {
  margin: 0px;
  padding: 0px;
}

li {
  list-style-type: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  vertical-align: top;
  max-width: 100%;
}

/* âœ…Typography */

html {
  font-family: "SpoqaHanSans";
  font-size: 62.5%;
  line-height: 1.4;
}

body {
  font-size: 1.4rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: 100%;
  font-weight: normal;
}

/* âœ…Commons */
.clearfix::before,
.clearfix::after {
  content: " ";
  display: table;
}

.clearfix::after {
  clear: both;
}

